import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Autoplay, Navigation } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import "swiper/css/autoplay";
import "./Swiper.css";

const text1 =
  "Serdecznie Polecamy! Rewelacyjne podejcie do noworodka i wysokiej jakoci zdjcia. Jestem bardzo zadowolona z sesji z Synkiem. Pani Fotograf pena profesjonalizmu oraz pasji do tego co robi, czego efektem s przepikne, naturalne zdjcia. Wspaniaa pamitka na cae ycie  Bardzo dzikujemy! -Natalia Nana Nana ";
const text2 =
  "Polecam sesja w miej atmosferze peen profesjonalizm.-Agnieszka Lekner";
const text3 =
  "Pani fotograf robi pikne zdjcia. Ma ciekawe pomysly na nie.Dziki niej mamy pikn pamitk z naszym synkiem.Szczerze polecam! -Urszula Stachniewicz ";
const text4 =
  "Sesja odbya sie w cudownej, spokojnej atmosferze zdjcia przepikne, zdobi nasza cian i kady si zachwyca. Peen profesjonalizm i zaangaowanie  Polecam serdecznie.-Joanna Puciennik";
const text5 =
  "Bardzo polecam Pani Mariole. Bardzo profesjonalny fotograf. Zdjcia wykonane po mistrzowsku., a co najlepsze nie byy pozowane, Pani Mariola potrafia uchwyci kady moment.-Justyna Kiliaska";

function createSlide() {
  return (
    <>
      <SwiperSlide>
        <p className="text">{text1}</p>
      </SwiperSlide>
      <SwiperSlide>
        <p className="text">{text2}</p>
      </SwiperSlide>
      <SwiperSlide>
        <p className="text">{text3}</p>
      </SwiperSlide>
      <SwiperSlide>
        <p className="text">{text4}</p>
      </SwiperSlide>
      <SwiperSlide>
        <p className="text">{text5}</p>
      </SwiperSlide>
    </>
  );
}

export default () => {
  return (
    <Swiper
      modules={[Pagination, Autoplay]}
      slidesPerView={1}
      autoplay={{ delay: 2000 }}
      pagination={{ clickable: true }}
    >
      {createSlide()}
    </Swiper>
  );
};
